body {
  max-width: 640px;
  margin: 0 auto;
}
.cj_result {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 6;
  max-width: 640px;
}
.cj_result article {
    position: absolute;
    width: 80%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    padding: 10px;
}
.cj_result article .close {
      position: absolute;
      font-size: 20px;
      color: #333;
      right: -6px;
      top: -10px;
      cursor: pointer;
}
.cj_result article img {
      margin: 10px auto;
}
.cj-box {
  width: 76.38889%;
  padding: 8.88889% 20px;
  background: url(../../static/img/cj-bg.fc3c87d.png) no-repeat center;
  background-size: contain;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0 auto;
  overflow: hidden;
}
.cj-box.curr {
    background: url(../../static/img/cj-bg-curr.b5f1a59.png) no-repeat center;
    background-size: contain;
}
.cj-box ul {
    padding: 2.5%;
}
.cj-box ul li {
      width: 30%;
      text-align: center;
      margin-left: 2.5%;
      padding: 5px 0;
      padding-right: 4px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      cursor: pointer;
      border-radius: 5px;
      background: #fff;
      position: relative;
      /*	display: flex;
			justify-content: center;
			align-items: center;
			text-align: center;*/
}
.cj-box ul li:nth-of-type(n+4) {
        margin-top: 3%;
}
.cj-box ul li:nth-of-type(5) {
        padding: 0 0;
}
.cj-box ul li:nth-of-type(5) img {
          width: 100%;
          margin-top: 0;
          position: absolute;
          left: 50%;
          -webkit-transform: translate(-50%);
                  transform: translate(-50%);
}
.cj-box ul li.active {
        background: url(data:image/png;base64,) no-repeat center;
        background-size: 100%;
}
.cj-box ul li img {
        width: 70%;
        margin-top: 15%;
}
.toast {
  height: 40px;
  line-height: 40px;
  padding: 0 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -20px;
  text-align: center;
  /*background: rgba(0, 0, 0, .4);*/
  border-radius: 10px;
  background: #e1e1e1;
  font-size: 14px;
  color: #333;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
  z-index: 111;
}














































































